.header {

    position: fixed;
    top: 0;
    left: 0;
    height: 14vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 100;
    background-color: white;
    border-bottom: 1px solid rgba(129, 129, 129, 0.4);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;




    img {
        margin: 1.25rem 0 4rem 3rem;
        height: 60%;
        
    }
}
